    <label for="user-name">Name</label>
    <input type="text" id="user-name" v-model="userName" v-on:keyup.enter="addUserName" />
  </div>
</template>

<script>
export default {
  name: "TodoHello",
  data(){
    return {
      userName:""
    }
  },
  methods:{
    addUserName(){
      this.$store.commit('addName', this.userName)
      this.$emit("addName", this.userName); //Ïù¥Î≤§Ìä∏ Î∞úÏÉù
    }
  }
}
</script>

